.hide.hide {
    display: none;
}

.container {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;  
}

.window {
    background-color: #F2F2F7;
    border-radius: 18px;
    width: 70%;
    height: 75%;
    position: fixed;
}

.window-dark {
    background-color: #1C1C1E;
}

.window-light {
    background-color: #F2F2F7;
}

.bar {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 50px;
    border-top-left-radius: 18px;
    border-top-right-radius: 18px;
    display: flex;
    gap: 24px;
    align-items: center;
}

.bar-dark {
    background-color: #2C2C2E;
}

.bar-light {
    background-color: #E5E5EA;
}

.content {
    height: calc(100% - 50px);
    width: 100%;
    top: 50px;
    position: absolute;
}

.button-container {
    display: flex;
    height: 100%;
    align-items: center;
    padding-left: 20px;
    gap: 12px;
}

.button {
    width: 18px;
    height: 18px;
    border-radius: 50%;
    cursor: pointer;
}

.close-button {
    background-color: #DE3B3B;
}

.close-button:hover {
    background-color: #C81D1D;
}

.min-button {
    background-color: #DEA63B;
}

.min-button:hover {
    background-color: #CC911D;
}

.expand-button {
    background-color: #1EAE1C;
}

.expand-button:hover {
    background-color: #139910;
}

.bar-title {
    font-size: 20px;
    margin: 0;
}

.title-dark {
    color: white;
}

.title-light {
    color: black;
}