/* Main container */
.container {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    color: white;
}
  
/* Central login box styling */
.login-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 20px;
    padding: 40px;
}
  
/* Circular profile image */
.profile {
    width: 183px;
    height: 183px;
    border-radius: 50%;
    overflow: hidden;
    margin-bottom: 20px;
    background-color: #C9FFCE;
    display: flex;
    align-items: center;
    justify-content: center;
}
  
.profile-icon {
    width: 65%;
    height: 65%;
}
  
/* Username styling */
.username {
    font-size: 24px;
    font-weight: 600;
    margin-bottom: 20px;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.3);
}

.password-container {
    display: flex;
    align-items: center;
    justify-content: center;
}

/* Input field and button styling */
.login-form {
    display: flex;
    align-items: center;
    background-color: rgba(255, 255, 255, 0.3);
    outline: none;
    border-radius: 30px;
    border: none;
    padding: 8px 10px 8px 20px;
}
  
.password-input {
    border: none;
    font-size: 16px;
    outline: none;
    background-color: transparent;
    color: white;
}

.password-input::placeholder {
    color: white;
}
  
.login-button {
    background-color: transparent;
    border: none;
    border-radius: 50%;
    font-size: 24px;
    margin-left: 10px;
    padding: 10px;
    cursor: pointer;
    color: white;
    height: 28px;
    width: 28px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid white;
}
  
.login-button:hover {
    background-color: rgba(255, 255, 255, 0.3);
}

.login-arrow {
    height: 200%;
    width: 200%;
}
  
/* Help icon styling */
.help-icon {
    background-color: transparent;
    border: none;
    border-radius: 50%;
    font-size: 18px;
    margin-left: 10px;
    cursor: pointer;
    color: white;
    height: 28px;
    width: 28px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid white;
}

.help-tooltip.help-tooltip {
    color: white;
    background-color: rgba(255, 255, 255, 0.20);
}

/* Error shake */
@keyframes shake {
    0% { transform: translateX(0); }
    25% { transform: translateX(-5px); }
    50% { transform: translateX(5px); }
    75% { transform: translateX(-5px); }
    100% { transform: translateX(0); }
  }
  
  .shake {
    animation: shake 0.3s ease;
  }

/* Skip Button */
.skip-container {
    position: absolute;
    bottom: 40px; /* Adjust as necessary for spacing */
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.skip-button {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 16px;
    background: transparent;
    border: none;
    color: white;
    cursor: pointer;
    padding: 18px;
    border-radius: 30px;
    background-color: rgba(255, 255, 255, 0.2);
}

.skip-button:hover {
    background-color: rgba(255, 255, 255, 0.3);
}

.skip-icon {
    height: 20px;
    width: 20px;
}

.skip-text {
    margin-top: 10px;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.3);
}