/* Navbar styling */
.navbar {
    position: fixed;
    top: 0;
    width: 100%;
    height: 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 30px;
    z-index: 999;
}

.navbar-dark {
    background: rgba(0, 0, 0, 0.1);
    color: white;
}

.navbar-light {
    background: rgba(255, 255, 255, 0.25);
    color: black;
}

.left-nav {
    display: flex;
    gap: 40px;
}

.right-nav {
    display: flex;
    gap: 20px;
}

.nav-logo {
    font-weight: bold;
    font-size: 18px;
    cursor: pointer;
}

.nav-link {
    font-size: 18px;
    cursor: pointer;
    text-shadow: 1px 1px 10px rgb(0, 0, 0, 0.1);
}

.nav-link:hover {
    font-weight: bold;
}

.nav-link-selected {
    font-weight: bold;
}

.time {
    font-size: 18px;
    padding-right:50px;
}

/* Sidebar with Bunny Icons */
.desktop {
    position: fixed;
    top: 75px;
    right: 30px;
    display: flex;
    flex-direction: column;
    align-items: start;
}

.icon-item {
    margin-bottom: 10px;
    text-align: center;
    color: white;
    cursor: pointer;
    border-radius: 15px;
    padding: 10px;
}

.icon-item img {
    width: 88px;
    height: 88px;
    margin-bottom: 0px;
}

.icon-item p {
    font-size: 16px;
    margin-top: 5px;
    font-weight: bold;
    text-shadow: 1px 1px 10px rgb(0, 0, 0, 0.3);
    margin-bottom: 5px;
}

.icon-item:hover {
    background-color: rgba(0, 0, 0, 0.2);
}

/* Bottom Dock for Social Links */
.dock {
    position: fixed;
    bottom: 30px;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    padding: 15px 30px;
    border-radius: 20px;
}

.dock-dark {
    background: rgba(0, 0, 0, 0.2);
}

.dock-light {
    background: rgba(255, 255, 255, 0.25);
}

.dock img {
    width: 80px;
    height: 80px;
    margin: 0 10px;
    cursor: pointer;
}

.dock img:hover {
    opacity: 0.8;
    transform: translateY(-8%);
}

.dock-division {
    width: 1px;
    height: 80px;
    background-color: rgb(255, 255, 255, 0.5);
    margin-left: 10px;
    margin-right: 10px;
}

.dock-tooltip.dock-tooltip {
    font-size: 18px;
}

.tooltip-dark.tooltip-dark {
    background-color: rgb(10, 10, 13);
    color: white;
}

.tooltip-light.tooltip-light {
    background-color: rgb(242, 242, 247);
    color: black;
}

.theme img {
    width: 20px;
    height: 20px;
}

.theme:hover {
    background-color: rgb(10, 10, 13, 0.1);
    border-radius: 20%;
    width: 25px;
    height: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
}