/* Background */
@keyframes move {
    100%{
        transform: translate3d(0, 0, 1px) rotate(360deg);
    }
  }
  
  .background {
    position: fixed;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    background: #6f74e5;
    overflow: hidden;
  }
  
  .background span {
    width: 60vmin;
    height: 60vmin;
    border-radius: 50vmin;
    backface-visibility: hidden;
    position: absolute;
    animation: move;
    animation-duration: 1;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
  }
  
  .background span:nth-child(0) {
    color: #687fd9;
    top: 74%;
    left: 56%;
    animation-duration: 70s;
    animation-delay: -358s;
    transform-origin: -18vw -24vh;
    box-shadow: -100vmin 0 12.731664431091339vmin currentColor;
  }
  
