.help-container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    overflow: scroll;
}

.help-container-dark {
    color: white;
}

.help-container-light {
    color: black;
}

.help-container-dark span {
    color: #5E5CE6;
}

.help-container-light span {
    color: #5856D6;
}

.help-content-container {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    font-size: 1.4vw;
    align-items: center;
    margin-top: 40px;
    margin-bottom: 30px;
}


.help-divider {
    height: 65%;
    width: 1px;
}

.help-divider-dark {
    background-color: #FFFFFF;
}

.help-divider-light {
    background-color: #8E8E93;
}

.help-content {
    width: 40%;
}

.help-guide-container {
    display: flex;
    align-items: center;
    justify-content: center;
}

.help-table {
    width: 80%;
    border-collapse: collapse;
    font-size: 1.4vw;
}

.help-table td,
.help-table th {
    text-align: left;
    vertical-align: top;
    padding: 0px 30px 45px 30px;
}


.help-table td:first-child {
    font-weight: bold;
    width: 35%;
}

.table-header {
    display: inline-block;
    padding: 7px 20px 7px 20px;
    margin-right: 100px;
    border-radius: 50px;
    font-size: 1.4vw;
    color: black;
    font-weight: 600;
    background-color: #32ADE6;
}
